.section.movie{
    width:100%;
    padding:0 140px;
    background-color:#eaeef4;
    min-height:100vh;
}
body::-webkit-scrollbar {
    background: #e9e9e9;
    border-radius:20px;
    width:9px;
}
body::-webkit-scrollbar-thumb {
    background: #a2a2a2;
    border-radius: 20px;
}
.movie-list-box{
    width:100%;
    min-height:100vh;
    overflow:hidden;
    border: 1px solid #ececec;
    border-radius: 15px 10px 10px 15px;
    box-sizing:border-box;
    padding: 50px 0;
}